@media (min-width: 1440px) {
  .plans {
    padding-top: 82px;
  }
}
@media (min-width: 1440px) {
  .plans__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
.plans__item {
  margin-bottom: 20px;
  background-color: var(--color-bg-grey-second);
  padding: 22px 21px 33px 18px;
}
.plans__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .plans__item {
    position: relative;
    padding: 77px 62px 54px 52px;
    margin-bottom: 30px;
  }
}
@media (min-width: 1440px) {
  .plans__item {
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
    position: static;
    padding: 77px 51px 55px 52px;
    max-width: 570px;
  }
}
.plans__title {
  display: block;
  margin-bottom: 25px;
  position: relative;
  padding-left: 70px;
}
@media (min-width: 768px) {
  .plans__title {
    position: static;
    padding-left: 0;
    max-width: 340px;
    font-size: 36px;
    line-height: 1.5;
  }
}
@media (min-width: 1440px) {
  .plans__title {
    position: relative;
    max-width: 100%;
    padding-left: 162px;
    margin-bottom: 73px;
  }
}
.plans__title::after {
  content: "";
  display: block;
  position: absolute;
  width: 36px;
  height: 50px;
  background: url(../img/svg/cat-slim.svg) no-repeat center center/contain;
  left: 5px;
  top: -10px;
  bottom: 0;
  margin: auto 0;
}
@media (min-width: 768px) {
  .plans__title::after {
    width: 140px;
    height: 194px;
    left: auto;
    top: -25px;
    bottom: 0;
    right: 90px;
  }
}
@media (min-width: 1440px) {
  .plans__title::after {
    width: 70px;
    height: 97px;
    left: 10px;
  }
}
.plans__title::before {
  content: "";
  position: absolute;
  background: var(--color-bg-green);
  width: 50px;
  height: 50px;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .plans__title::before {
    width: 200px;
    height: 200px;
    left: auto;
    right: 62px;
  }
}
@media (min-width: 1440px) {
  .plans__title::before {
    width: 100px;
    height: 100px;
    left: 0;
  }
}
.plans__title--pro::after {
  background: url(../img/svg/cat-pro.svg) no-repeat center center/contain;
  height: 28px;
  width: 67px;
  left: -8px;
  top: 14px;
}
@media (min-width: 768px) {
  .plans__title--pro::after {
    width: 268px;
    height: 112px;
    left: auto;
    top: 0;
    bottom: 0;
    right: 28px;
  }
}
@media (min-width: 1440px) {
  .plans__title--pro::after {
    width: 134px;
    height: 56px;
    left: -15px;
  }
}
.plans__desc {
  padding-bottom: 22px;
  margin-bottom: 15px;
  position: relative;
}
@media (min-width: 768px) {
  .plans__desc {
    max-width: 340px;
    margin-bottom: 48px;
    font-size: 16px;
    line-height: 1.5;
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .plans__desc {
    max-width: 100%;
    margin-bottom: 23px;
    flex-grow: 1;
  }
}
.plans__desc::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background: var(--color-bg-grey-third);
  left: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .plans__desc::after {
    display: none;
  }
}
.plans__btn {
  display: inline-block;
  text-transform: uppercase;
  padding: 4px 50px 4px 0;
  font-family: var(--basic-family);
  color: var(--color-black-main);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  position: relative;
}
.plans__btn::after {
  content: "";
  position: absolute;
  background: url(../img/svg/icon-arrow.svg) no-repeat;
  width: 24px;
  height: 12px;
  right: 10px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  transition: 0.1s ease-in;
}
.plans__btn:hover::after {
  background: url(../img/svg/icon-arrow-hover.svg) no-repeat;
  width: 32px;
  right: 0;
}
.plans__btn:focus {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .plans__btn {
    font-size: 20px;
    line-height: 1.5;
  }
}
@media (min-width: 1440px) {
  .plans__btn {
    max-width: 167px;
  }
}